import {List, ListItem, Typography} from "@material-ui/core";
import React from "react";
import {IOpsSdk, PluginDefinition} from "@deliveryhero/opsportal";

const Plugs: React.FC<{ baseApi: IOpsSdk }> = ({baseApi}) => {
    return (
        <React.Fragment>

            <Typography variant="h3">
                List of loaded plugins
            </Typography>
            <hr/>
            <br/>

            <Typography variant="h5">By Calling the getPlugins() function on the OpsPortal API
                you can get information about the plugins that are available to the current user
            </Typography>


            <br/>
            <br/>
            <Typography variant="h5">By Calling the getPlugins() function on the OpsPortal API
                you can get information about the plugins that are available to the current user
            </Typography>
            <List>
                {
                    baseApi.getPlugins().map((p: PluginDefinition) => {
                        return <ListItem>{p.name}</ListItem>
                    })
                }
            </List>
        </React.Fragment>
    )
};

export default Plugs;
