import {IOpsSdk} from '@deliveryhero/opsportal';
import {Breadcrumbs, Chip, Paper, Toolbar} from "@material-ui/core";
import React from 'react';
import {Router, Link, Route, Switch} from "react-router-dom";
import './App.css';
import customHistory from "./history";
import Build from "./pages/Build";
import Calls from "./pages/Calls";
import Intro from "./pages/Intro";
import Plugs from "./pages/Plugs";
import User from "./pages/User";

export const App: React.FC<{ baseApi: IOpsSdk }> = ({baseApi}) => {
    return (
        <div className="pluginRoot">
            <Router history={customHistory}>
                    <Toolbar>
                        <Breadcrumbs aria-label="breadcrumb">
                            <Chip component={Link} to="/" label={'Getting started'} style={{cursor:'pointer'}} />
                            <Chip component={Link} to="user" label={'User Info'} style={{cursor:'pointer'}} />
                            <Chip component={Link} to="plugs" label={'Plugins'} style={{cursor:'pointer'}} />
                            <Chip component={Link} to="calls" label={'Making Requests'} style={{cursor:'pointer'}} />
                            <Chip component={Link} to="build" label={'Building your plugin'} style={{cursor:'pointer'}} />
                        </Breadcrumbs>
                    </Toolbar>
                <div className={'pluginView'}>
                    <Paper style={{flex: 1, width: '100%', padding: '5em'}}>
                        <Switch>
                            <Route path="/" component={() => <Intro baseApi={baseApi}/>} exact={true}/>
                            <Route path="/user" component={() => <User baseApi={baseApi}/>} exact={true}/>
                            <Route path="/plugs" component={() => <Plugs baseApi={baseApi}/>} exact={true}/>
                            <Route path="/calls" component={() => <Calls baseApi={baseApi}/>} exact={true}/>
                            <Route path="/build" component={() => <Build baseApi={baseApi}/>} exact={true}/>
                        </Switch>
                    </Paper>
                </div>
            </Router>
        </div>
    );
}

export default App;
